menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	/* border: 1px solid red; */
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
    box-sizing: border-box;
}

@font-face {
    font-family: 'Roboto Flex';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

:root{
    --MAIN-COLOUR: #fafaf9;
    --BG-COLOUR: #e5e7eb;
	--TEXT-LIGHT: #a3a3a3;
    --ACCENT-1: #0ea5e9;
    --ACCENT-2: #facc15;
    font-family: 'Roboto Flex', sans-serif;
    font-size: 1rem;
    line-height: 1.5;
}

body{
	height: 100vh;
	background-color: var(--MAIN-COLOUR);
	display: grid;
	grid-template-columns: minmax(250px, 20%) 1fr;
}

ul>li{
	list-style: none;
}

.close-button{
	cursor: pointer;
	width: 25px;
	height: 25px;
}

/* NAV */

nav{
	background-color: var(--BG-COLOUR);
	display: flex;
	flex-direction: column;
}

.nav-wrapper{
	flex: 1;
	display: flex;
	flex-direction: column;
	gap: 2rem;
	padding: 1rem;
}

.nav-top{
	display: flex;
	flex-direction: column;
}

#nav-folder-list{
	display: flex;
	flex-direction: column;
}

.nav-top li,
.nav-project{
	width: fit-content;
	cursor: pointer;
	margin-left: 1rem;
}

#new-project-open{
	height: 3rem;
	background: var(--ACCENT-1);
	color: var(--MAIN-COLOUR);
	font: inherit;
	font-weight: bold;
	border: none;
	cursor: pointer;
	margin-top: auto;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 0.2rem;
}

#new-project-open>img{
	filter: invert(83%) sepia(63%) saturate(2%) hue-rotate(20deg) brightness(105%) contrast(98%);
	height: 30px;
	width: 30px;
}

#new-project-open>p{
	margin-top: 2px;
}

/* MODAL DIALOG */

.modal[open]{
	box-sizing: border-box;
	background-color: var(--MAIN-COLOUR);
	width: 70ch;
	display: flex;
	flex-direction: column;
	border: none;
	border-radius: 2rem;
	padding: 2rem;

}

.modal::backdrop{
	background: black;
	opacity: .4;
}

.modal>h2{
	font-size: 2rem;
	text-transform: uppercase;
	margin-bottom: 1.5rem;
}

.modal>img{
	align-self: flex-end;
}

.modal>form{
	display: flex;
	flex-direction: column;
}

.modal>form .input-wrapper{
	display: flex;
	flex-direction: column;
	margin-bottom: 1rem;
}

.modal input,
.modal textarea,
.modal select{
	background-color: var(--BG-COLOUR);
	font: inherit;
	border: none;
	border-radius: 1rem;
	padding: 0.3rem 0.8rem;
}

.modal input:focus,
.modal textarea:focus,
.modal select:focus{
	outline: 2px solid var(--ACCENT-1);
}

.modal textarea{
	resize: vertical;
	min-height: 1.5rem;
}

.modal select{
	appearance: none;
	background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
	background-repeat: no-repeat;
	background-position: right 5px center;
	background-size: 28px;
}

.bottom-row{
	display: flex;
	gap: 1rem;
}

.bottom-row>.input-wrapper{
	flex: 1;
}


.modal button{
	align-self: flex-end;
	background-color: var(--ACCENT-1);
	color: var(--MAIN-COLOUR);
	width: 10ch;
	font: inherit;
	font-weight: bold;
	cursor: pointer;
	border: none;
	border-radius: 2rem;
	margin-top: 1rem;
	margin-right: 1rem;
	padding: 1rem 1rem;
}

/* MAIN */ 

main{
	padding: 3rem;
}

.folder-page-wrapper{
	display: flex;
	flex-direction: column;
}

.title-wrapper{
	margin-bottom: 2rem;
}

.header-wrapper>h1{
	width: fit-content;
	position: relative;
	font-size: 3rem;
	font-weight: bold;
}

.title-wrapper>p{
	color: var(--TEXT-LIGHT);
}

#projectDesc{
	font-size: 1.2rem;
	margin-top: 0.5rem;
}

.header-wrapper img{
	visibility: hidden;
	cursor: pointer;
	width: 30px;
	height: 30px;
	position: absolute;
}

.header-wrapper:hover img{
	visibility: visible;
}

.edit-project-button{
	top: 1.5rem;
	right: -3rem;
}

.delete-project-button{
	top: 1.5rem;
	right: -5.5rem;
}

.to-do-list{
	display: flex;
	flex-direction: column;
	gap: 0.5rem;
	margin-bottom: 1.5rem;
}

.to-do-list>li{
	display: flex;
	align-items: center;
	gap: 0.8rem;
}

.to-do-list>li>p{
	color: var(--TEXT-LIGHT);
	font-size: 1rem;
}

.to-do-list>li>img{
	height: 20px;
	width: 20px;
	cursor: pointer;
	visibility: hidden;
}

.to-do-list>li:hover>img{
	visibility: visible;
}

#new-to-do-open{
	height: 2rem;
	background: var(--BG-COLOUR);
	font: inherit;
	font-size: 1rem;
	font-weight: bold;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	border: none;
	border-radius: 1rem;
	padding: 1rem;
	gap: 0.3rem;
}

#new-to-do-open>p{
	margin-top: 2px;
}

#new-to-do-open>img{
	width: 25px;
	height: 25px;
